body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    display: flex;
    position: fixed;
    left: 50%;
    transform: translate(-50%);
    top:0;
    width:100%;
    height: 0px;
    padding: 20px;
    background-color: none;
    z-index: 100;
    box-sizing: border-box;
}

.logo {
    margin: 0px 10px;
}

.logo img {
    width: 162px;
    height: 42px;
}

.phone-num {
    margin: 20px 10px 10px;
    min-width: 110px;
}

.menus {
    margin: 0px 10px 0px auto;
}
.menus .menus-btn {
    list-style: none;
    display: flex;
    margin: 10px 20px;
    text-align: center;
}
.menus li {
    width: 90px;
    text-decoration: none;

}
.menus a {
    text-decoration: none;
    color: black;
}
.menus a:hover {
    color: salmon;
    transition: 0.5s;
}

.projects-dropdown {
    background-color: none;
}

.projects-dropdown-menus {
    margin: 30px 0px 10px;
    display: none;
}

.projects-dropdown:hover .projects-dropdown-menus {
    display: block;
    
}

.toggle-btn {
    width: 18px;
    height: 24px;
    display: none;
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
    text-indent: -9999px;

}

.sr-only {
    text-indent: -9999px;
}

.icon-bar {
    display: block;
    width: 18px;
    height: 3px;
    background-color: #292929;
    border-radius: 2px;
    margin: 4px 0px;
    width: 100%;
}



/* MEDIA */

@media (max-width: 1024px) {
    .header {
        height: auto;
        display: block;
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        left: 0;
        transform: translate(-1%);
        padding: 10px;
        background-color: white;
    
    }

    .header .toggle {
        display: none;
    }

    .header .toggle-on {
        display: block;
        background-color: white;
    }

    .phone-num {
        display: none;
    }

    .menus .menus-btn {
        display: block;
        text-align: left;
        margin: 20px 20px;
        /* border-top: 1px solid #cacaca; */
    }

    .menus .menus-btn li {
        width: 100%;
        margin : 0;
        padding: 16px 0;
        border-bottom: 1px solid #cacaca;
    }

    .menus .projects-dropdown-menus {
        margin: 0 40px;
    }

    .header .toggle-btn {
        display: inline;
    }
}
