/* input */
.ipt_top, select{
    width:400px;
    height: 60px;
     
}
input{
    width:385px;
    height: 60px;
    padding: 3px 3px 3px 12px;
    border: none;
    border-bottom: 1px solid black;
    font-size: 16px;
    color: black;
}
input:-ms-input-placeholder{
    color: #969696;  
}
input::placeholder{
    color: #969696;  
}

select{
    width:400px;
    height: 60px;
    padding: 0px 45px 0px 12px;
    border: none;
    font-size: 16px;
    border-bottom: 1px solid black;
    color: #969696;
}
input:focus, select:focus{
    outline: 0;
}

input[type=checkbox]{
    position: relative;
    top:25%;
    width:12px;
    height: 12px;
}

/* button */
button{
    width:140px;
    height: 39px;
    cursor:pointer;
    border: none;
    font-size: 16px;
    color: #969696;
    background-color: rgb(51, 51, 51);
    float: center;
}

/* grid */

.estimation-grid-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(3, 80px) 150px 200px auto 100px auto;
}
.grid-item:nth-child(-2n+6) label{
    float: right;
}
.grid-item:nth-child(9) input{
    /* border: none; */
    width: 100%;
    padding-bottom: 100px;
}

.grid-item:nth-child(10) .checkbox-container{
    display: flex;
    justify-content: center;
    margin-top: 50px;
}
.grid-item:nth-child(11) .button-container{
    display: flex;
    justify-content: center;  
}

.grid-item:nth-child(7){
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 4;
    grid-row-end: 5;
}
.grid-item:nth-child(8){
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 5;
    grid-row-end: 6;
}
.grid-item:nth-child(9){
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 6;
    grid-row-end: 7;
}


.grid-item:nth-child(10){
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 7;
    grid-row-end: 8;
}

.grid-item:nth-child(11){
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 8;
    grid-row-end: 9;
}

.submit:hover{
    background-color: black;
}

/* MEDIA */

@media (max-width: 1024px) {
    .Estimates-container{
        margin-top:0px; /*for header*/
    }
    .estimation-grid-container{
        display: block;
    }
    .grid-item{
        display: flex;
        justify-content: center;
        margin-bottom: 50px;
    }
    .grid-item:nth-child(7),.grid-item:nth-child(8){
        display: inline-block;
    }

    .grid-item:nth-child(9) .ipt_bot {
        width: 385px;
    }
}

