/* 텍스트상자 */
.wrap {
    width: 70px;
    height: 30px;
    position: absolute;
    left: -35px;
    bottom: 50px;
    display: flex;
    justify-content: left;
    align-items: center;
    background-color:white;
    border-radius: 5px;
}
.wrap .info {
    /* overflow: hidden; */
}
.info .title {
    padding: 5px 10px ;
    font-size: 12px;
    font-weight: 600;
}
.info .close {
    position: absolute;
    top: 5px;
    right: 5px;
    color: #888;
    width: 8px;
    height: 8px;
    background: url('https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/overlay_close.png');
    background-size: cover;
}
.info .close:hover {cursor: pointer;}
.info:after {
    content: '';
    position: absolute;
    margin-left: -12px;
    left: 50%;
    bottom: -12px;
    width: 22px;
    height: 12px;
    background: url('https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/vertex_white.png');
}

